import { FunctionComponent, KeyboardEvent } from 'react';
import styled, { css } from 'styled-components';

import { Card, Count, Flex, Icon, useI18n } from '@pega/cosmos-react-core';

import { CaseViewProps } from './CaseView.types';

export interface UtilitiesSummaryProps {
  items?: CaseViewProps['utilitiesSummaryItems'];
  onClick?: () => void;
}

export const StyledUtilitiesSummary = styled.div(({ theme }) => {
  return css`
    width: 2.75rem;
    padding: ${theme.base.spacing} 0;
    cursor: pointer;
  `;
});

const UtilitiesSummary: FunctionComponent<UtilitiesSummaryProps> = ({ items = [], onClick }) => {
  const t = useI18n();
  return items.length ? (
    <Card
      interactive
      tabIndex={0}
      role='button'
      container={{ direction: 'column', alignItems: 'center', gap: 3 }}
      aria-label={`${t('utilities_summary')}. ${t('expand_utilities_panel')}`}
      as={StyledUtilitiesSummary}
      onClick={onClick}
      onKeyDown={(e: KeyboardEvent<HTMLDivElement>) => {
        if (e.key === ' ' || e.key === 'Enter') onClick?.();
      }}
    >
      {items.map(item => {
        return (
          <Flex
            container={{
              direction: 'column',
              alignItems: 'center',
              gap: 0.5
            }}
            item={{ grow: 1 }}
            key={item.iconName}
          >
            <Icon name={item.iconName} />
            {item.count !== undefined && <Count variant='default'>{item.count}</Count>}
          </Flex>
        );
      })}
    </Card>
  ) : null;
};

export default UtilitiesSummary;
